/*
use localStorage to save and load cart data 

*/

const database = {
  Save(cartList) {
    let data = JSON.stringify(cartList)
    localStorage.setItem('cartList', data)
  },
  Load() {
    return JSON.parse(localStorage.getItem('cartList')) || []
  },
  Clear() {
    localStorage.removeItem('cartList')
  },
}

const state = () => ({
  items: [],
})
const getters = {
  ItemIndexByID: (state) => (itemId) => {
    return state.items.findIndex((element) => {
      return element.id === itemId
    })
  },
  ItemQuantity: (state, getters) => (itemId) => {
    let index = getters.ItemIndexByID(itemId)
    return state.items[index].quantity
  },

  CartTotalPrice: (state, getters, rootState, rootGetters) => {
    if (state.items.length > 0) {
      let list = state.items.map((element) => {
        let price = rootGetters['products/ProductInfo'](element.id).price
        return price * element.quantity
      })
      return list.reduce((a, b) => a + b)
    } else {
      return 0
    }
  },
}
const mutations = {
  Item_Add(state, item) {
    state.items.push(item)
    database.Save(state.items)
  },

  Item_EditQuantity(state, { index, quantity }) {
    state.items[index].quantity = quantity
    database.Save(state.items)
  },

  Item_Remove(state, index) {
    state.items.splice(index, 1)
    database.Save(state.items)
  },

  Clear(state) {
    state.items.splice(0)
    database.Clear()
  },
  LoadData(state) {
    state.items = database.Load()
  },
}
const actions = {
  ItemAdd({ state, getters, commit }, item) {
    let index = getters.ItemIndexByID(item.id)

    if (index >= 0) {
      let quantity = item.quantity + state.items[index].quantity
      commit('Item_EditQuantity', { index, quantity })
    } else {
      commit('Item_Add', item)
    }
  },

  ItemEdit({ commit, getters }, item) {
    let index = getters.ItemIndexByID(item.id)
    let quantity = item.quantity
    commit('Item_EditQuantity', { index, quantity })
  },

  ItemRemove({ commit, getters }, item) {
    let index = getters.ItemIndexByID(item)

    commit('Item_Remove', index)
  },

  Clear({ commit }) {
    commit('Clear')
  },

  CartLoad({ commit }) {
    commit('LoadData')
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}
