<template>
  <div class="slideshow">
    <!-- issue: change image position -->
    <ul
      class="slideshow-container"
      :style="{ transform: Position, width: Width }"
      @mouseenter="SlideStop"
      @mouseleave="SlideGo"
    >
      <li class="slideshow-item" v-for="pic in list" :key="pic">
        <img :src="require(`../assets/${pic}`)" :alt="pic" />
      </li>
    </ul>
    <ul class="slideshow-dot-group">
      <li
        class="slideshow-dot-item"
        :class="{ 'slideshow-dot-Active': currentIndex === index }"
        v-for="(pic, index) in list"
        :key="index"
        @click="Change(index)"
      ></li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentIndex: 0,
      timer: null,
    }
  },
  props: {
    list: {
      type: Array,
      default: () => ['banner01.jpg', 'banner02.jpg', 'banner03.jpg'],
    },
  },
  computed: {
    Width() {
      return `${this.list.length * 100}%`
    },
    Position() {
      let currentOffset = (this.currentIndex / this.list.length) * 100
      return `translate(-${currentOffset}%, 0)`
    },
  },
  methods: {
    Slide() {
      if (this.currentIndex < this.list.length - 1) {
        this.currentIndex = this.currentIndex + 1
      } else {
        this.currentIndex = 0
      }
    },
    SlideStop() {
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    SlideGo() {
      if (this.timer === null) {
        this.timer = window.setInterval(this.Slide, 2000)
      }
    },
    Change(index) {
      this.SlideStop()
      this.currentIndex = index
      this.SlideGo()
    },
  },

  mounted() {
    this.SlideGo()
  },
  beforeDestroy() {
    this.SlideStop()
  },
}
</script>

<style lang="scss">
.slideshow-container {
  max-height: 200px;
  overflow: hidden;
  @include flex(row);
  transition: transform 0.5s ease-in-out;

  @include lg {
    max-height: 600px;
  }
  .slideshow-item {
    width: 100%;
  }
}

.slideshow-dot-group {
  margin: 10px auto;
  @include flex(row);

  .slideshow-dot-item {
    width: 10px;
    height: 10px;
    padding: 3px;
    margin: 0px 15px;
    border-radius: 999rem;
    background-color: $color-lightGray;
    transition: 0.3s all;
    cursor: pointer;
  }

  .slideshow-dot-Active {
    transform: scale(1.2);
    background-color: $color-orange;
  }
}

.slide-right-enter {
  transform: translateX(100%);
}

.slide-right-leave-to {
  transform: translateX(-100%);
}

.slide-left-enter {
  transform: translateX(-100%);
}

.slide-left-leave-to {
  transform: translateX(100%);
}

.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
}
</style>
