<template>
  <header class="main-nav">
    <button class="menu-btn" @click="menuToggle()">
      <span></span>
      <span></span>
      <span></span>
    </button>
    <nav class="menu" :class="{ 'menu-show': menuShow }">
      <router-link class="menu-link" to="/order" @click.native="menuHide()">
        線上訂餐
      </router-link>
      <router-link class="menu-link" to="/" @click.native="menuHide()">
        <font-awesome-icon icon="home" />
      </router-link>
      <router-link class="menu-link" to="/shoppingCart" @click.native="menuHide()">
        <font-awesome-icon icon="shopping-cart" />
      </router-link>
      <router-link class="menu-link" to="/login" @click.native="menuHide()">
        <font-awesome-icon icon="user" />
      </router-link>
    </nav>
  </header>
</template>

<script>
export default {
  name: 'AppHeader',
  data() {
    return {
      menuShow: false,
    }
  },
  methods: {
    menuToggle() {
      this.menuShow = !this.menuShow
    },
    menuHide() {
      this.menuShow = false
    },
  },
}
</script>

<style lang="scss">
.main-nav {
  width: 100%;
  height: 30px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background: $color-gray;

  .menu-btn {
    width: 25px;
    height: 25px;
    box-sizing: border-box;
    position: absolute;
    top: 2.5px;
    right: 30px;
    @include flex(column, space-between, center);
    @include lg {
      display: none;
    }

    span {
      width: 25px;
      height: 3px;
      border-radius: 5px;
      background-color: $color-white;
    }
  }

  .menu {
    width: 100%;
    position: absolute;
    top: 30px;
    left: 0;
    background: $color-gray;
    transition: 0.3s;
    transform: translateX(-100%);

    @include lg {
      position: relative;
      top: 0px;
      transform: translateX(0);
      @include flex(row, flex-end, center);
    }
  }

  .menu-link {
    display: block;
    position: relative;
    color: $color-white;
    padding: 10px 0;
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.3);

    @include lg {
      display: inline-block;
      width: 100px;
      box-shadow: none;
    }

    &::after {
      content: '';
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
      border-bottom: 5px solid $color-orange;
      transform: scale(0);
      transition: all 0.5s;
    }
    &:hover::after {
      transform: scale(1);
    }
  }

  .menu-link + .menu-link::before {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $color-gray;
  }

  a.router-link-exact-active {
    color: $color-orange;
  }

  .menu-show {
    transform: translateX(0);
  }
}
</style>
