/*現存商品列表 */

import goodsData from '@/assets/goodsData.json'

const state = () => ({
  list: goodsData,
})
const getters = {
  ProductInfo: (state) => (id) => {
    return state.list.find((element) => {
      return element.id === id
    })
  },
  FilterList: (state) => (type) => {
    if (type !== 'all') {
      return state.list.filter((element) => {
        return element.type === type
      })
    } else {
      return state.list
    }
  },
}
const mutations = {}
const actions = {}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}
